<template>
  <div @submit.prevent="updateHandler">
    <form>
      <label>Title</label>
      <input input="text" v-model="project.title" required />

      <label>Details:</label>
      <textarea v-model="project.details" required></textarea>

      <div class="project-status">
        <div class="radio-container">
          <input
            type="radio"
            v-model="project.isCompleted"
            :value="!project.isCompleted"
          />
          <div class="radio-overlay">
            <span
              v-if="project.isCompleted"
              class="material-icons-outlined completed"
            >
              done
            </span>
            <span v-else class="material-icons-outlined"> close </span>
          </div>
        </div>
        <label
          >Project Status:
          <span
            :class="[
              project.isCompleted ? `status completed` : `status on-going`,
            ]"
            v-if="project.isCompleted"
            >Completed</span
          >
          <span v-else>Ongoing</span></label
        >
      </div>

      <button>Update Project</button>
    </form>
  </div>
</template>
<script>
export default {
  props: ["id"],
  data() {
    return {
      project: [],
    };
  },
  mounted() {
    (async () => {
      try {
        this.isLoadingProjects = true;
        const res = await fetch("http://localhost:3000/projects/" + this.id);
        if (res.ok) {
          const data = await res.json();
          this.project = data;
        } else {
          this.isFailedFetchProjects = "There is a Problem Fetching the data";
        }
      } catch (err) {
        console.log(err);
      }
    })();
  },
  methods: {
    async updateHandler() {
      try {
        this.isLoadingProjects = true;
        const res = await fetch("http://localhost:3000/projects/" + this.id, {
          method: "PATCH",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            title: this.project.title,
            details: this.project.details,
            isCompleted: this.project.isCompleted,
          }),
        });
        if (res.ok) {
          this.$router.push({ name: "home" });
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>
<style scoped>
form {
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
}
label {
  display: block;
  color: #bbb;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 1px;
  margin: 20px 0 10px 0;
}
input {
  padding: 10px;
  border: 0;
  border-bottom: 1px solid #ddd;
  width: 100%;
  box-sizing: border-box;
}
textarea {
  border: 1px solid #ddd;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  height: 100px;
}

form button {
  display: block;
  margin: 20px auto 0;
  background-color: #00ce89;
  color: #fff;
  padding: 10px;
  border: 0;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
}

.project-status {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.project-status label {
  display: flex;
  justify-content: center;
  align-items: center;
  white-space: pre;
}

input[type="radio"] {
  width: max-content;
  position: relative;
  top: 5px;
  opacity: 0;
  width: 18px;
  height: 18px;
  z-index: 1;
  cursor: pointer;
}

span {
  color: rgba(170, 92, 18, 0.904);
}
.completed {
  color: rgb(12, 180, 12);
}

.radio-container {
  position: relative;
  margin-right: 10px;
}

.radio-overlay {
  position: absolute;
  left: -8px;
  top: 5px;
}
</style>
